<template>
    <main class="app-main">
        <section class="top">
            <p class="title"><md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>编辑基本信息</p>
        </section>
        <div class="con-con">
            <div class="image-reader-item add first-item">
                <md-image-reader
                        name="reader0"
                        @select="onReaderSelect"
                        @complete="onReaderComplete"
                        @error="onReaderError"
                        is-multiple
                ></md-image-reader>
                <div class="des">头像</div>
                <img class="head-pic" :src="userInfo.img" alt="">
            </div>
            <md-input-item
                    ref="input0"
                    title="单位名称"
                    placeholder="单位名称"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.unitName"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="单位统一社会代码"
                    placeholder="单位统一社会代码"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.creditCode"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="单位法人"
                    placeholder="单位法人"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.legalPerson"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="单位规模"
                    :clearable="true"
                    placeholder="单位规模"
                    @click.native="onTypeShow"
                    v-model="userInfo.unitScale"
                    is-amount
            >
            </md-input-item>
            <md-input-item
                    ref="input0"
                    title="联系人"
                    placeholder="联系人"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.name"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="联系电话"
                    placeholder="联系电话"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.phone"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="优势学科"
                    placeholder="优势学科"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.advantageDisciplines"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="行业领域"
                    placeholder="行业领域"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.genre"
            ></md-input-item>
            <md-input-item
                    ref="input0"
                    title="主营产品"
                    placeholder="主营产品"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.product"
            ></md-input-item>
            <md-input-item
                    title="单位简介"
                    @click.native="onProfile"
                    v-model="userInfo.intro"

            >
                <md-icon
                        slot="right"
                        name="arrow-right"
                        size="lg"
                ></md-icon>
            </md-input-item>
        </div>
        <md-button class="submitBtn" type="default" @click="editInfo" style="border: 0!important">保存</md-button>
        <div style="height: 45px"></div>
        <md-picker
                ref="typePicker"
                v-model="isEducShow"
                :data="Education"
                @confirm="onEducConfirm"
                title="选择操作类型"
        ></md-picker>
        <md-picker
                ref="typePicker"
                v-model="isTypeShow"
                :data="expertType"
                @confirm="onTypeConfirm"
                title="选择单位规模"
        ></md-picker>
        <md-picker
                ref="typePicker"
                v-model="isFieldShow"
                :data="field"
                @confirm="onFieldConfirm"
                title="选择操作类型"
        ></md-picker>
    </main>
</template>

<script>
import { Field, CellItem, Toast, ImageReader, FieldItem, Picker, Icon, InputItem, Button } from 'mand-mobile'
import { Education, expertType, field } from '../../../utils/select'
import { editInfo, fileUpload, getInfo } from '../../../apiUrl/personal/info'
import { getDictionary } from '../../../apiUrl/login'

export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [ImageReader.name]: ImageReader,
    [Picker.name]: Picker,
    [FieldItem.name]: FieldItem,
    [InputItem.name]: InputItem
  },
  data () {
    return {
      imageList: { // 上传照片列表
        reader0: [
          '//img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/27fb7f097ca218d743f816836bc7ea4a',
          '//manhattan.didistatic.com/static/manhattan/insurancePlatform_spu/uploads/c2912793a222eb24b606a582fd849ab7'
        ],
        reader1: []
      },
      renderInfo: {
        genre: '',
        type: '',
        academic: ''
      },
      userInfo: {
        unitName: '',
        img: '',
        genre: '',
        creditCode: '',
        legalPerson: '',
        unitScale: '',
        name: '',
        phone: '',
        intro: '',
        advantageDisciplines: '',
        genre: 1,
        product: ''
      },
      isEducShow: false,
      isTypeShow: false,
      isFieldShow: false,
      expertType: [],
      Education: [],
      field: []
    }
  },
  created () {
    this.getInfo()
  },
  mounted () {
    this.getDictionary()
    this.getInfo()
    this.userInfo.intro = window.localStorage.getItem('infoText')
  },
  methods: {
    getInfo () {
      console.log(this.userInfo)
      getInfo().then((res) => {
        this.userInfo = res.data
        this.userInfo.intro = window.localStorage.getItem('infoText')
        if (!res.data.img) {
          this.userInfo.img = require('../../../assets/personal/5.png')
        }
      })
    },
    editInfo () {
      editInfo(this.userInfo).then((res) => {
        if (res.status === 200) {
          Toast.succeed('编辑成功！')
          setTimeout(() => {
            this.$router.push('/personal/entCheckInfo')
          }, 2000)
        }
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 回退
    goBack () {
      window.localStorage.infoText = ''
      this.$router.push('/personal')
    },
    onProfile () {
      this.$router.push({ path: '/personal/Profile', query: { type: 'ent' } })
    },
    onEducShow () {
      document.activeElement.blur()
      this.isEducShow = true
    },
    onTypeShow () {
      document.activeElement.blur()
      this.isTypeShow = true
    },
    onFieldShow () {
      document.activeElement.blur()
      this.isFieldShow = true
    },
    // 类型选择确认
    onEducConfirm (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      // this.Education = columnsValue[0].text
      this.renderInfo.academic = columnsValue[0].label
      this.userInfo.academic = columnsValue[0].value
    },
    // 类型选择确认
    onTypeConfirm (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      // this.Education = columnsValue[0].text
      this.userInfo.unitScale = columnsValue[0].value
      this.renderInfo.unitScale = columnsValue[0].value
    },
    // 类型选择确认
    onFieldConfirm (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      // this.Education = columnsValue[0].text
      this.userInfo.genre = columnsValue[0].value
      this.renderInfo.genre = columnsValue[0].label
    },
    onReaderSelect (name, { files }) {
      files.forEach(file => {
        console.log(file)
      })
      Toast.loading('图片读取中...')
    },
    onReaderComplete (name, { dataUrl, file }) {
      // this.userInfo.img = dataUrl
      Toast.hide()
      const args = new FormData()
      args.append('file', file)
      args.append('fileName', file.name)
      fileUpload(args).then((res) => {
        this.userInfo.img = res.file
      }).catch((e) => {
        Toast.failed(e)
      })
    },
    onReaderError (name, { msg }) {
      Toast.failed(msg)
    },
    onDeleteImage (name, index) {
      const demoImageList = this.imageList[name] || []
      demoImageList.splice(index, 1)
      this.$set(this.imageList, name, demoImageList)
    },
    getDictionary () {
      getDictionary({ type: 'genre' }).then((e) => {
        this.field.push(e.data)
      })
      getDictionary({ type: 'academic' }).then((e) => {
        this.Education.push(e.data)
      })
      getDictionary({ type: 'unitScale' }).then((e) => {
        this.expertType.push(e.data)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    .top
        .title
            background rgba(2,25,85,1);
    .con-con
        padding-left 30px
        margin-bottom 60px
        margin-top 88px
        background:rgba(255,255,255,1);
        box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
        border-radius:12px;
        padding-top 88px
        .head-portrait
            background:rgba(2,25,85,0);
            border-radius:12px;
            img
                width 140px
                height 140px
                margin-top -140px
        .phone
            font-size:28px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(2,25,85,1);
            line-height:36px;
            img
                width 25px
                height 28px
                margin-left 20px
    /deep/.md-field-item-title
        color #666
        font-weight 20
    /deep/.md-cell-item-body
        padding 0px 20px
        text-align left
    /deep/.md-cell-item-left
        margin-right 10px
    /deep/.md-cell-item
        height 91px
    /deep/.md-cell-item-body:before
        top:91px
        bottom auto
    /deep/.md-button
        width:660px;
        height:88px;
        background:rgba(2,25,85,1);
        border-radius:12px;
        margin 0 auto
        color #fff
    /deep/.md-image-reader
        height 117px
        float left
    /deep/.md-field-item-right
        margin-right 20px
    /deep/.md-image-reader-file
        width 140px!important
        right 0!important
        left auto!important
        height 100px
    /deep/.md-field-item-title
        text-align left
    /deep/.md-button
        border 0!important
    /deep/.md-button.default:after
        border 0!important
    .image-reader-item
        height 100px
        color: #666;
        font-size 0.32rem
        line-height 100%
        font-weight: 20;
        text-align left
        border-bottom  0.02rem solid #e2e4ea
        position relative
        .head-pic
            width 80px
            height 80px
            position absolute
            right 31px
            top 10px
        .des
            color rgba(51,51,51,1)
            font-weight 500
            width 160px
            text-align left
            display inline-block
            height 100px
            line-height 100px
            font-size 28px
    /deep/.md-field-item-title
        font-weight 500
        color rgba(51,51,51,1)
        font-size 28px
    /deep/.md-input-item-input
        text-align right!important
        color #999999FF
    .first-item
        height 117px
</style>
