import request from '../../utils/request'
import apiPath from '../url'
import qs from 'qs'
// 查询用户信息
export function getInfo (data) {
    data = qs.stringify(data)
    console.log(data)
    return request({
        url: apiPath.personal.info.getInfo,
        method: 'post',
        data: data,
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    })
}
// 编辑用户信息
export function editInfo (data) {
    data = qs.stringify(data)
    // console.log(data)
    return request({
        url: apiPath.personal.info.editInfo,
        method: 'post',
        data: data
    })
}
// 上传头像
export function fileUpload (data) {
    console.log(data)
    return request({
        url: apiPath.personal.info.fileUpload,
        method: 'post',
        data: data
    })
}
